import React, { Component } from 'react'
import "../css/home.css"
import MyImage from "../images/myImage.jpg"
export default class Home extends Component {
    render() {
        return (
            <div>
                <div className="container ">
                    <div className="row">
                        <div style={{zIndex:-100}} className="col-12">
                            <img style={{ width:"300px" }} src={MyImage} alt=""/>   
                        </div>
                        <div className="col-md-12 font-weight-light">
                        
                            <h6>Hi</h6>
                            <h4>I'm Abhijit Kumar</h4>
                            <h2>Creative Web Designer &amp;</h2>
                            <h1>A <b className="font-weight-light text-primary"> Frontend Developer</b></h1>
                            {/* <hr className="bg-primary col-md-3 float-left" /> */}
                        </div>
                    </div>
                    <br />
                    <div className="row">
                        <button className="col-md-3 m-3 ion-button btn">See More...</button>
                    </div>
                </div>
            </div>
        )
    }
}
